*,
:after,
:before {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  position: relative;
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial,
    sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  min-height: 100%;
  padding: 0;
  margin: 0;
  background-color: #fff;
}

.show-menu {
  display: none;
  cursor: pointer;
  border: 0;
}

@media (max-width: 900px) {
  .show-menu {
    position: fixed;
    display: block;
    bottom: 30px;
    right: 0;
    padding: 10px 15px;
    background: #333;
    color: #fff;
    border-radius: 5px 0 0 5px;
    z-index: 10;
  }
}
 
.popup-header {
  background-color: #f0f0f0;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

.popup-header strong {
  font-size: 18px;
}

 

.popup-body p {
  margin-bottom: 5px;
}

.popup-content {
  width: 100% !important;
  overflow-x: auto !important; /* Gerekirse yatay kaydırmayı etkinleştir */
}

.popup-body {
  margin-top: 10px;
  width: 100% !important;
  overflow-x: auto !important;
}  
.popup-table td {
  border: 1px solid #ddd; /* Border rengi ve kalınlığı */
  padding: 8px; /* Hücre içeriğinin etrafındaki boşluk */
  text-align: left; /* Metnin hücre içinde hizalanması */
}

.popup-table th {
  background-color: #f2f2f2; /* Başlık hücrelerinin arka plan rengi */
}